import style from './Carreira.module.css'
import CustomizedTimeline from '../pages/CustomizedTimeline'
import CardEmpresa from '../pages/CardEmpresa'

import imgHa from '../../utils/img/ha.jpeg'
import imgZara from '../../utils/img/zara.jpeg'
import imgMfx from '../../utils/img/mfx.jpeg'

export default function Carreira(){
    return (
        <div className={style.containerCarreira}>
            <div className={style.tituloLinhaTempo}>
                <h2 className={style.tituloLinha}>Linha do tempo:</h2>
            </div>
            <CustomizedTimeline />

            <div className={style.resumoCarreira}>
                <CardEmpresa nome="Hospital Amrica" img={imgHa} paragrafo1="No Hospital Amrica, dei incio  minha jornada de trabalho como aprendiz de Tecnologia da Informao. Naquela poca, conciliava minhas responsabilidades dirias no hospital com as aulas noturnas na faculdade. Essa experincia se revelou extremamente enriquecedora, pois me possibilitou aplicar os conceitos que estava aprendendo nos primeiros semestres diretamente na prtica profissional."
                paragrafo2="Fui agraciado com um suporte excepcional por parte da equipe, a qual me proporcionou total liberdade para sanar minhas dvidas e observar profissionais mais experientes em plena ao. Minha funo envolvia o atendimento primrio aos usurios, sendo responsvel por solucionar questes relativas a problemas de hardware e software mais simples. Essa etapa fundamental me proporcionou uma slida base para a construo da minha carreira profissional."/>

                <CardEmpresa nome="Zaraplast S.a" img={imgZara} paragrafo1="Na Zaraplast, ocorreu meu segundo envolvimento com o mercado de trabalho, onde assumi o papel de estagirio de Tecnologia da Informao. No entanto, fui incumbido de muito mais responsabilidades em comparao  minha posio anterior como aprendiz. Alm das interaes com o suporte, tambm me foram confiadas tarefas relacionadas  infraestrutura. Essas atribuies adicionais refletiram a confiana depositada em mim e na minha capacidade de lidar com desafios complexos."
                 paragrafo2="Apesar de ter vivenciado e aprendido com as experincias ligadas a suporte e infraestrutura, eu sempre soube que minha verdadeira paixo era a programao. Com o apoio dos meus gestores, pude finalmente concretizar esse desejo ao implementar minha primeira pgina web que no se tratava de um projeto pessoal. Especificamente, desenvolvi a lista de ramais da empresa, que atendia aos mais de 1000 funcionrios. Apesar da aparente simplicidade, esse projeto trouxe-me grande satisfao, uma vez que a nova funcionalidade da intranet da empresa foi amplamente aprovada por todos os colaboradores." />
                
                <CardEmpresa nome="Grupo MFX" img={imgMfx} paragrafo1="No Grupo MFX, vivenciei a minha terceira incurso no mercado de trabalho, onde atualmente estou empregado. Minha trajetria teve incio como desenvolvedor front-end, uma oportunidade que surgiu graas ao reconhecimento do meu trabalho por um prestador de servios da Zaraplast. Esse reconhecimento abriu as portas para que eu me unisse  equipe."
                paragrafo2="No decorrer do meu tempo aqui, evolui para a funo de consultor Fluig, atendendo a uma variada gama de clientes, tanto a nvel nacional quanto internacional. No entanto, minha busca constante pelo aprendizado me levou a explorar outras tecnologias. Enquanto j trabalhava com JavaScript no front-end, aprofundei meus conhecimentos em Node.js. Esse mergulho me permitiu aprimorar minhas habilidades e desenvolver projetos mais complexos, que no s abrangiam a interface do usurio, mas tambm exigiam a implementao de solues no back-end para funcionar de maneira integral. Realizei a entrega de projetos altamente escalveis, capazes de atender a uma grande quantidade de usurios, a uma variedade de clientes distintos." />
            </div>
        </div>
    )
} 