import style from './Projetos.module.css'

import CardProjeto from '../pages/CardProjeto'

import getApetImg from '../../utils/img/getapet.png'
import toughtsImg from '../../utils/img/tougths.png'
import podcasterImg from '../../utils/img/podcastr.jpg'
import portifolioImg from '../../utils/img/portifolio.png'
import cpaImg from '../../utils/img/cpa.png'
import gestFatImg from '../../utils/img/gestaoFat.jpg'
import gestUsuImg from '../../utils/img/gestUsua.jpg'
import cotEleImg from '../../utils/img/cotacaoEletronica.jpg'
import prescImg from '../../utils/img/prescricaoMedica.png'

export default function Projetos() {
    return (
        <div className={style.projetosContainer}>
            <h1 className={style.projetosTitulo}>Principais projetos:</h1>

            <div className={style.projetos}>

            <CardProjeto titulo="Prescrio Mdica" tipo="Pessoal" paragrafo="O projeto de prescrio mdica tem como objetivo de ser um sistema completo de prescrio de medicamento a pacientes. O sistema foi desenvolvido inteiro do zero, desde a modelagem do banco,  at as telas no front-end utilizando Angular. Nele  possvel cadastrar os pacientes e os mdicos, os mdicos tem a possibilidade de cadastrar medicamentos, prescrever receitas mdicas e visualizar o histrico de prescries dos usurios." imagem={prescImg} linkGh="https://github.com/oguilima/fullProject-PrescricaoMedica" tecnologias={[{
                    TITULO: "Angular",
                    DESCRICAO: "Foi utilizado no front-end do projeto."
                }, {
                    TITULO: "Angular Material",
                    DESCRICAO: "Utilizado como biblioteca de estilizao para o projeto."
                }, {
                    TITULO: "PostgreSQL",
                    DESCRICAO: "Foi utilizado PostgreSQL como banco de dados."
                }, {
                    TITULO: "NodeJs",
                    DESCRICAO: "Foi utilizado o NodeJs para desenvolvimento do back-end do projeto."
                }, {
                    TITULO: "Express",
                    DESCRICAO: "Foi utilizado o express para facilitar a criao das rotas e de outras funcionalidades do sistema."
                }, {
                    TITULO: "Swagger",
                    DESCRICAO: "Foi utilizado o Swagger para documentar as rotas da aplicao."
                }, {
                    TITULO: "Jest",
                    DESCRICAO: "Utilizado para testes automatizados."
                }]} />

                <CardProjeto titulo="Produto Acabado" tipo="Crown Cork" paragrafo="O sistema de cadastro de produtos acabados foi elaborado na plataforma fluig, visando estabelecer um controle mais efetivo sobre os produtos finais da Crown. Esse sistema permite uma gesto mais precisa do processo de cadastramento dos itens destinados  produo da empresa. Atravs da automatizao desse procedimento, tornou-se possvel a anlise dos indicadores que identificam eventuais pontos crticos e mtricas relevantes. Esse enfoque possibilitou um gerenciamento mais eficiente e abrangente do projeto em pauta." imagem={cpaImg} tecnologias={[{
                    TITULO: "Fluig",
                    DESCRICAO: "Foi utilizado como plataforma para digitalizao do processo."
                }, {
                    TITULO: "JQuery",
                    DESCRICAO: "Utilizado para o front-end, onde desenvolvi o formulrio principal e as telas auxiliares do projeto."
                }, {
                    TITULO: "MySql",
                    DESCRICAO: "Foi utilizado MySql para gravao das informaes e consultas para as telas auxiliares do projeto."
                }, {
                    TITULO: "Protheus",
                    DESCRICAO: "Foi utilizado o Protheus como ERP para a gravao do produto acabado que  utilizado para a fabricao do item."
                }]} />


                <CardProjeto titulo="Get A Pet" tipo="Pessoal" paragrafo="O Get A Pet  uma rede social onde consiste em ser uma plataforma onde usurios possam adotar pets de outras pessoas, foi realizado atravs do curso do Mateus Battisti da Hora de Codar de NodeJs, esse projeto  bem completo com funcionalidades de criar usurio, incluir um pet no sistema, realizar um agendamento para visita do pet e concluir a adoo. Foi bem divertido faze-lo e consegui aprender muito com ele." imagem={getApetImg} linkGh="https://github.com/oguilima/getAPet" tecnologias={[{
                    TITULO: "NodeJs",
                    DESCRICAO: "NodeJs foi utilizado para desenvolver o back-End da aplicao."
                }, {
                    TITULO: "Express",
                    DESCRICAO: "Foi utilizado o express para facilitar a criao das rotas e de outras funcionalidades do sistema."
                }, {
                    TITULO: "ReactJs",
                    DESCRICAO: "Foi utilizado ReactJs para desenvolver o front-end da aplicao."
                }, {
                    TITULO: "MongoDB",
                    DESCRICAO: "Foi utilizado o MongoDB como base de dados para toda aplicao."
                }]} />

                <CardProjeto titulo="Gesto de Faturamento" tipo="Zaraplast S.a" paragrafo="O sistema de gesto de faturamento da Zaraplast foi realizado atravs da plataforma Fluig, alcanando assim a total digitalizao do processo. Este sistema inovador desempenha um papel crucial na soluo das questes enfrentadas pelas equipes de vendas, faturamento e logstica. Atravs de um portal unificado, todas as informaes so disponibilizadas de forma gil e eficaz, promovendo uma abordagem centralizada que otimiza a experincia." imagem={gestFatImg} tecnologias={[{
                    TITULO: "Fluig",
                    DESCRICAO: "Foi utilizado como plataforma para digitalizao do processo."
                }, {
                    TITULO: "JQuery",
                    DESCRICAO: "Utilizado para o front-end, onde desenvolvi o formulrio principal e as telas auxiliares do projeto."
                }, {
                    TITULO: "MySql",
                    DESCRICAO: "Foi utilizado MySql para gravao das informaes e consultas para as telas auxiliares do projeto."
                }, {
                    TITULO: "Protheus",
                    DESCRICAO: "Foi utilizado o Protheus como ERP para a emisso de notas fiscais."
                }, {
                    TITULO: "PC Factory - PPI",
                    DESCRICAO: "Foi utilizado o SmartSolution como ERP para a fabricao dos produtos da empresa."
                }]} />

                <CardProjeto titulo="Thoughts" tipo="Pessoal" paragrafo="O Thoughts  mais um projeto desenvolvido no curso do Mateus Battisti da Hora de Codar de NodeJs, o projeto consiste em ser uma rede social para compartilhar diversos tipos de pensamentos, o projeto  bem legal e  possvel cadastrar usurios novos, publicar pensamentos e verificar os pensamentos de outros usurios." imagem={toughtsImg} linkGh="https://github.com/oguilima/Toughts" tecnologias={[{
                    TITULO: "NodeJs",
                    DESCRICAO: "Foi utilizado para desenvolver o Back-End da aplicao."
                }, {
                    TITULO: "Express",
                    DESCRICAO: "Foi utilizado o express para facilitar a criao das rotas e de outras funcionalidades do sistema."
                }, {
                    TITULO: "ReactJs",
                    DESCRICAO: "Foi utilizado ReactJs para desenvolver o front-end da aplicao."
                }, {
                    TITULO: "MySql",
                    DESCRICAO: "Foi utilizado o MySql como base de dados para toda aplicao."
                }]} />


                <CardProjeto titulo="Gesto de Usurios" tipo="Rede Oba" paragrafo="O sistema de administrao de usurios foi concebido para permitir a extrao simplificada de relatrios relacionados aos utilizadores da plataforma Fluig. Atravs desse sistema,  possvel obter informaes abrangentes sobre diversos aspectos, incluindo os usurios com privilgios administrativos, aqueles que aceitaram as polticas de utilizao da plataforma, bem como os grupos e funes atribudas a cada usurio. Todas essas informaes so prontamente acessveis ao administrador do sistema, proporcionando uma gesto eficaz e conveniente." imagem={gestUsuImg} tecnologias={[{
                    TITULO: "Fluig",
                    DESCRICAO: "Foi utilizado como plataforma para digitalizao do processo."
                }, {
                    TITULO: "JQuery",
                    DESCRICAO: "Utilizado para o front-end, onde desenvolvi as telas do projeto."
                }, {
                    TITULO: "MySql",
                    DESCRICAO: "Foi utilizado MySql para consulta das informaes relacionadas aos usurios."
                }]} />


                <CardProjeto titulo="Podcastr" tipo="Pessoal" paragrafo="O Podcastr  um projeto desenvolvido atravs da Next-Level-Week, semana de desenvolvimento realizado pela Rocketseat. O sistema foi desenvolvido em uma semana e consiste em ser um player de udio com foco em podcasts, onde aps o cadastro do podcast, o mesmo fica disponvel para ser escutado na plataforma. O mesmo tem funes completas de controle de reproduo do podcast." imagem={podcasterImg} linkGh="https://github.com/oguilima/Podcastr-NLW" tecnologias={[{
                    TITULO: "ReactJs",
                    DESCRICAO: "Foi utilizado ReactJs para desenvolver o front-end da aplicao."
                }, {
                    TITULO: "NextJs",
                    DESCRICAO: "Foi utilizado para desenvolver o front-end da aplicao."
                }, {
                    TITULO: "Axios",
                    DESCRICAO: "Foi utilizado para fazer conexes de API para aplicao."
                }]} />

                <CardProjeto titulo="Polticas de Acesso" tipo="Vaccinar" paragrafo='O "Sistema de Polticas de Acesso" opera como um mecanismo atravs do qual os documentos fornecidos pelo setor de Recursos Humanos so disseminados para todos os usurios da plataforma. O objetivo central  garantir que cada usurio seja informado e possa aceitar as polticas estabelecidas pela empresa. Esse projeto reveste-se de uma complexidade significativa, pois abrange todos os utilizadores da plataforma. Foram implementadas estratgias para assegurar a escalabilidade, e o resultado foi uma realizao bem-sucedida desse empreendimento desafiador.' imagem={gestUsuImg} tecnologias={[{
                    TITULO: "Fluig",
                    DESCRICAO: "Foi utilizado como plataforma para digitalizao do processo."
                }, {
                    TITULO: "JQuery",
                    DESCRICAO: "Utilizado para o front-end, onde desenvolvi as telas do projeto."
                }, {
                    TITULO: "MySql",
                    DESCRICAO: "Foi utilizado MySql para consulta das informaes relacionadas aos usurios."
                }]} />

                <CardProjeto titulo="Portiflio" tipo="Pessoal" paragrafo="Meu portflio tem como propsito compartilhar um pouco sobre mim, minha trajetria profissional, os projetos que desenvolvi e o conhecimento que adquiri ao longo do tempo. Nele, voc encontrar insights sobre meu percurso e o progresso da minha carreira, evidenciando meu constante aprendizado e aprimoramento  medida que avano." linkGh="https://github.com/oguilima/portifolio" imagem={portifolioImg} tecnologias={[{
                    TITULO: "ReactJs",
                    DESCRICAO: "Foi utilizado ReactJs para desenvolver o front-end da aplicao."
                }, {
                    TITULO: "MaterialUi",
                    DESCRICAO: "Foi utilizado para aproveitamento de componentes e cones."
                }]} />

                <CardProjeto titulo="Cotao Eletrnica" tipo="Lifemed" paragrafo='O projeto de "Cotao Eletrnica" visa a digitalizao de um processo que previamente era conduzido de forma manual pelos compradores. A partir da gerao de uma solicitao de compra de um produto no sistema Protheus, o comprador assume a responsabilidade de identificar as empresas cadastradas para fornecer esse produto. Em seguida,  feita a requisio de cotaes, permitindo que as empresas forneam oramentos diretamente. Aps essa etapa, o comprador avalia as cotaes e seleciona o fornecedor vencedor. Nesse momento, o sistema automaticamente emite um pedido de compra com base nas informaes pertinentes. Esse projeto representa uma melhoria substancial na eficincia do processo, eliminando a interveno manual e agilizando a obteno dos produtos necessrios.' imagem={cotEleImg} tecnologias={[{
                    TITULO: "Fluig",
                    DESCRICAO: "Foi utilizado como plataforma para digitalizao do processo."
                }, {
                    TITULO: "JQuery",
                    DESCRICAO: "Utilizado para o front-end, onde desenvolvi o formulrio principal e as telas auxiliares do projeto."
                }, {
                    TITULO: "MySql",
                    DESCRICAO: "Foi utilizado MySql para gravao das informaes e consultas para as telas auxiliares do projeto."
                }, {
                    TITULO: "Protheus",
                    DESCRICAO: "Foi utilizado o Protheus como ERP para a gravao do cotao do produto."
                }]} />
            </div>

        </div>
    )
} 